class App extends Component {
  constructor(props) {
    super(props);
    this.state = { number: 0 }
  }
  componentWillMount() {
    console.log('3');
  }
  componentDidMount() {
    console.log('5 ');
  }

  shouldComponentUpdate(newProps, newState) {
    console.log('6 ');
    if (newState.number < 15) {
      return true;

    }
    return false;
  }
  componentWillUpdate() {
    console.log('7');

  }
  componentDidUpdate() {
    console.log('8')
  }
  handleClick = () => {
    this.setState({
      number: this.state.number + 1
    })
  };
  render() {
    console.log('4render()');
    return (
      <div className="App">
        <ul>
          <li><IndexLink to="/about">About</IndexLink ></li>
          <li><IndexLink to="/inbox">Inbox</IndexLink ></li>
        </ul>
        <div>
          <p>{this.state.number}</p>

          <button onClick={this.handleClick}>+</button>
          {this.state.number < 10 ? <Sub number={this.state.number} /> : null}
        </div>
        {this.props.children}
      </div>
    );
  }
}

export default App;
